body {
  margin: 0;
  padding: 20px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(rgb(7, 27, 82)),
    to(rgb(0, 128, 128))
  );
  background: -webkit-linear-gradient(
    right,
    rgb(7, 27, 82) 0%,
    rgb(0, 128, 128) 100%
  );
  background: -o-linear-gradient(
    right,
    rgb(7, 27, 82) 0%,
    rgb(0, 128, 128) 100%
  );
  background: linear-gradient(
    to left,
    rgb(7, 27, 82) 0%,
    rgb(0, 128, 128) 100%
  );
}

h1 {
  color: white;
}



